import * as React from 'react';
import RecipeReviewCard from './Card';
import { useState } from 'react';
import { Paper,Card } from '@mui/material';
import Legend from './Legand';
import SelectArea from './SelectArea';
import Form from 'react-bootstrap/Form';
import './Sidebar.css'; 


const SideBar = ({Statisc , STCNTR,stvisability }  )=>{

    

    


    
      
    const changeCenter = (centerValue) => {
         
      const ALL_CENTER= [ 21.4136950,39.8960253]
        const ARAFAT_CENTER = [21.351104, 39.977129];
        const MOZDALIFA_CENER = [21.371167, 39.936773];
        const MINA_CENTER = [21.405520, 39.900130];
        let vcount;
        let vsum;
        let newCenter;
        switch (centerValue) {
          case "ALL":
              newCenter = ALL_CENTER;
              break;  
          case "MINA":
              newCenter = MINA_CENTER;
              break;
            case "MOZDALIFA":
              newCenter = MOZDALIFA_CENER;
              break;
            case "ARAFAT":
              newCenter = ARAFAT_CENTER;
              break;
            default:
              newCenter = null; // Handle the "ALL" case or any other default
          }
         
          if (newCenter) {
            console.log('New Center:', newCenter);
            STCNTR(newCenter,centerValue);
             
          } else {
            console.warn(`Unhandled center value: ${centerValue}`);
          }
      
          
        };

      
        











    
    return(
    <>
       <div>
    <Form.Select className='classic'
    
    onChange={(e) => changeCenter(e.target.value)} 
    aria-label="select" size='lg' 
    
    style={ { Color: 'blue', borderRadius:10, border:"none", backgroundColor:'rgb(255,255,255,.4)', padding:'0.2rem 2rem 0.2rem 2rem', marginTop:".7rem" } }>
            <option style={{backgroundColor:'rgb(255,255,255,.4)'}} value="ALL" >All</option>
            <option value="MINA">MINA</option>
            <option value="MOZDALIFA">MOZDALIFA</option>
            <option  value="ARAFAT">ARAFAT</option>
     </Form.Select></div>
    <RecipeReviewCard  StatiscticValues={Statisc}/>

        <Legend stv={stvisability}/>
    
    
    </>

        
    );
}
export default SideBar;
