
import SideBar from './Sidebar';
import './MapApp.css'; 
import React, { useEffect, useState,  useRef} from 'react';
import {useMap, MapContainer, TileLayer, GeoJSON, CircleMarker,Marker, ZoomControl,SVGOverlay,FeatureGroup ,LayersControl, LayerGroup} from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
