import { Visibility } from '@material-ui/icons';








const MapApp=()=>{

    const [curentmap, setCurentMap] = useState(null);
    const [center, setCenter]= useState([ 21.4136950,39.8960253]);
    const [geojsonData, setGeojsonData] = useState(null);
    const [pointgeojsonData, setpointGeojsonData] = useState(null);
    const [iconSize, setIconSize] = useState(20); 
    const [sidebarOpen, setsidebarOpen] = useState(true);
    const [mapZoom, setMapZoom] = useState(13);
    const lvisiblity =useRef ([true,true,true,true])
    const layerRef1 = useRef(L.featureGroup())
    const layerRef2 = useRef(L.featureGroup())
    const layerRef3 = useRef(L.featureGroup())
    const layerRef4 = useRef(L.featureGroup())
    


    
    
    
    
    
    const TOTALPILGRIMS= useRef(1570668);
    const TotalWC = useRef(100479);
    const TotalWCComplex = useRef(3422);
    const mobileWC= useRef(262)

    
        
    const mapStatisticsf = (mWC)=>{
      const tp = TOTALPILGRIMS.current;
      const tw = TotalWC.current;
      const twc= TotalWCComplex.current;
      mobileWC.current=mWC;
     
      
      return ({
      'TOTAL PILGRIMS':tp, 
      'TOTAL WC':tw,
      'TOTAL WC Complex':twc,
      'MOBILE WC':mWC
    });}
        
    const setTableData =(data)=>{
      const features = data.features;
      
      const attributes = features.flatMap((feature) => feature.properties);
      
      const columnNames = Object.keys(data.features[0].properties);
      const tableColumns = columnNames.map(columnName => ({
        header: columnName,
        accessorKey: `properties.${columnName}`
      }));
      
      return ([tableColumns,features]);}
    //data= "\\jcbu-fas\FAS-FS\TEST FOLDER\Map"
    
    const groupByCoulum1 =(data)=>{
      const features = data.features;
      
      const attributes = features.flatMap((feature) => feature.properties);
      
      const columnNames = Object.keys(attributes);
      //console.log(columnNames)
      //const a= pd.DataFrame(attributes., { columns: columnNames })
      //const df = new DataFrame(attributes,columnNames);
      //console.log(df.count())  
      
    }


    
      // Load and process data using D3
     
      const groupByCoulum = (data,coulum) => {
        
        const features = data.features;   
        const attributes = features.flatMap((feature) => feature.properties);
        
        const columnNames = Object.keys(attributes);
        const groupedData = d3.group(attributes, d => d[coulum]);

         const Counts = d3.rollups(attributes, (D) => D.length, (d) => d[coulum] ); // [["Adelie", 152], …]
        const areaValues= {'ALL':[3422,100479]}
        // Access grouped values:
        for (const groupKey of groupedData.keys()) {
          
          const groupValues = groupedData.get(groupKey);
         // console.log(`Group ${groupKey}:${groupValues.length } `);
          
          const DataSum = d3.sum(groupValues, (d) => d.WC_COUNT);
          areaValues[groupKey]= [ groupValues.length , DataSum]
          
         
     
         
        }
        console.log(areaValues);
        return(areaValues);
       
      }
        
        





    const groupByCount1 = (data, property) => {
      const groupedData = {};
      data.forEach(item => {
        const key = item.properties[property];
        groupedData[key] = groupedData[key] || 0;
        groupedData[key]++;
        
      });
      console.log("aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa",groupedData);
      return groupedData;
    };
    
    const groupBySum = (data, property) => {
      const groupedData = {};
      data.forEach(item => {
        const key = item[property];
        groupedData[key] = groupedData[key] || 0;
        groupedData[key] += item.value; 
      });
      return groupedData;
    };



      const sumGeoJSONColumn=(geoJsonData, columnName) =>{
        try {
          
          let sum = 0;
      
          
          geoJsonData.features.forEach(feature => {
            const columnValue = feature.properties[columnName];

            if (!isNaN(columnValue)) {
              sum += parseFloat(columnValue);
            }
          });

          return sum;
      
        } catch (error) {
          console.error('Error loading GeoJSON file:', error);
        }
      }
      
          
        
        

    
    

    useEffect(() => {

        // Fetch the GeoJSON file from the public folder
        fetch(WCBLOCKS_q)
          .then((response) => response.json())
          .then((data) => setGeojsonData(data))
          .catch((error) => console.error('Error fetching GeoJSON:', error));
      
      fetch(WCBLOCKS_Point)
      .then((response) => response.json())
      .then((pointdata) => setpointGeojsonData(pointdata))
      .catch((error) => console.error('Error fetching point GeoJSON:', error));
      
    }, []);

    const countGeoJSONColumn=(geoJsonData, columnName,countValue) =>{
      try {
        
        let count = 0;
    
        
        geoJsonData.features.forEach(feature => {
          const columnValue = feature.properties[columnName];
          
          if (countValue) {
            if(columnValue === countValue ){          
              count+=1;
            } 
          }else{count+=1}
        });
        return count;
    
      } catch (error) {
        console.error('Error loading GeoJSON file:', error);
      }
    }

    
   {pointgeojsonData && groupByCoulum(pointgeojsonData,'AREA')}
   



    //const mobile_WC= countGeoJSONColumn(pointgeojsonData,"CATEGORY",'متنقلة')
       
    const mobile_WC= mobileWC.current



    
    const onEachPolygonFeature = (feature, layer) => {
    
        if (feature.properties) {
          let popupContent = '<table class="popup-table">'; // Start the table
          //TOTALPILGRIMS.current += feature.properties.TOTAL_PILGRIMS
         //TOTALPILGRIMS.current = feature.properties.TOTAL_PILGRIMS
         //TotalWC.current += feature.properties.TOTAL_WC_COUNT    
         
          
          for (const [key, value] of Object.entries(feature.properties)) {
            popupContent += `<tr><td><strong>${key}</strong></td><td>${value}</td></tr>`;}
      
          popupContent += '</table>'; // End the table
      
          layer.bindPopup(popupContent);}};
         
        
    const  getColor = d => {
    
            return d === -1 ? '#b6b5b5' :
                  d < 41 ? '#0a4811' :
                   d < 81  ? '#10e929' :
                   d < 121 ? '#969407' :
                   d > 121 ? '#960707' :
                              '#FFEDA0';}
    
    
        
        
    const style= (feature) =>{
            return {
                fillColor: getColor(feature.properties.WC_PER_PERSON),
                weight: 2,
                opacity: 100,
                color: 'white',
                dashArray: '3',
                fillOpacity: 0.7
            };}

    
                
            const culureZoom = (zoom)=>{
              return zoom < 5 ? 60 :
                    zoom < 13 ? 50 :
                    zoom < 15  ? 40 :
                    zoom < 16 ? 30 :
                    zoom < 20 ? 10 :
                                  5
            }
      
            
      const customIcon= L.icon({
          iconUrl: iconSquare,
          iconSize: [10, 10], 
          iconAnchor: [0, 0],
          popupAnchor: [0, -16],}); 

      const iconBound =(x,y)=>{
         const bound =[
          [x-.001, -0.08],
          [51.5, -0.06],
        ]
        return
      }
    
    
      const mapStatistics = mapStatisticsf(mobile_WC)
    
       
     let newMapCenter;

     const GetMapCenter=(value,area)=>{
       setCenter(value);
       const byAreaValues= groupByCoulum(pointgeojsonData,'AREA')
       console.log("byAreaValues", byAreaValues, "center:",area)
       TotalWC.current= byAreaValues[area][1];
       TotalWCComplex.current= byAreaValues[area][0];
      
      console.log('ssssssssssssss',value);
      }

    const SetMapCenter=({cntr})=>{
      const mp= useMap();
      mp.flyTo(center,13.5);
     useEffect(()=> {if (lvisiblity[0]===false){mp.removeLayer(layerRef1.current)}
      if (curentmap){setMapZoom(mp.getZoom);}},[lvisiblity.current[0]])
      
      
      
      
    return null }


    const filteredData =(d,d2)=> geojsonData.features.filter(feature => {
      
      return ( (feature.properties.WC_PER_PERSON >=d)&&(feature.properties.WC_PER_PERSON<=d2)  );
    });
    
    
 /*   const LayercVisability=()=>{
      const mp= useMap();
      
      console.log('cureeeent map')
      if (layerRef1.current) {
        mp.addLayer(layerRef1.current)
        console.log("layeraded")
      
      
      }else{mp.removeLayer(layerRef1.current.leafletElement);
      
      console.log('removed');
      }
      return null
  
    }*/






const changeVisiblity= (d,n)=>{
  
  const v= lvisiblity
  v[n]= d
  console.log(v);
   lvisiblity.current= (v);
  
   
   
}

const AddLAYER =({l})=>{
 

    


    const mpl= useMap();
    console.log("bbbbb",lvisiblity.current[0]);
   if (lvisiblity.current[1]=== false){mpl.remove(layerRef1)}
    mpl.flyTo(center);
  
    return null;
  }
  
   

//{  geojsonData &&lvisiblity[1] && <GeoJSON data={filteredData(-1,40)} onEachFeature={onEachPolygonFeature} style={style} />}

    
    return (
    
 
    <div className="mapAppContaier">
    
    <div className='mapApp'>
   
     <Button 
        onClick={() => setsidebarOpen(!sidebarOpen)}
        aria-controls="leftSidbar"
        aria-expanded={sidebarOpen}
        style={{display:'none'}}
      >
       =
      </Button>
 <> { sidebarOpen &&
    <div className='leftSidbar' id='leftSidbar' >

   
     
    <SideBar Statisc={mapStatistics}  STCNTR={GetMapCenter} stvisability={changeVisiblity} /> 
    
    
    
    </div>}</>

    <div className='nwcmap'>
        

  <MapContainer 
        center={center} 
        zoom={18} 
        attributionControl  ={false} 
        style={{ height: '78vh', width: '400vw', 'border-radius': '10px'} }  
        whenCreated={setCurentMap}
       
        >
        
        <LayersControl>
        
        <TileLayer 
        attribution='@NWC'
        url="https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}"/>
        
        
      
       
         
         <LayerGroup>
        
         <LayersControl.Overlay checked= {true} name='NULL' >
        <FeatureGroup  ref={layerRef1} >
        {geojsonData && <GeoJSON data={filteredData(-1,40)} onEachFeature={onEachPolygonFeature} style={style}/>}
         </FeatureGroup> 
         </LayersControl.Overlay  >


         <LayersControl.Overlay checked= {true} name='40-80'> 
         <FeatureGroup  ref={layerRef2} >
        {  geojsonData  && <GeoJSON data={filteredData(41,80)} onEachFeature={onEachPolygonFeature} style={style}/>}
         </FeatureGroup> 
          </LayersControl.Overlay  >


          <LayersControl.Overlay checked= {true} name='80-120'> 
         <FeatureGroup  ref={layerRef3} >
        {  geojsonData  && <GeoJSON data={filteredData(81,120)} onEachFeature={onEachPolygonFeature} style={style}/>}
        </FeatureGroup > </LayersControl.Overlay  >


        
        <LayersControl.Overlay checked= {true} name='>120'> 
        <FeatureGroup  ref={layerRef4} >
        {   geojsonData&& <GeoJSON data={filteredData(121,500)} onEachFeature={onEachPolygonFeature} style={style}/>}
       </FeatureGroup></LayersControl.Overlay  >




        </LayerGroup>
             
           
           
            <LayersControl.Overlay checked= {false} name='POINTS' mapZoom='18' >
           <FeatureGroup  > 
            { pointgeojsonData && pointgeojsonData.features.map((feature) => (
            
 
            <Marker 
            key={feature.properties.No}
            position={[feature.geometry.coordinates[1],feature.geometry.coordinates[0]]}
            title={feature.properties.New_CODE}
            icon={customIcon}
            ></Marker>
            
            ))}

          </FeatureGroup> 
          
          </LayersControl.Overlay>   
        
          
          

          
           
   </LayersControl>       
 <SetMapCenter cntr={center}/>
 
<AddLAYER l={lvisiblity} />
 

  </MapContainer>,



                
          



 


 
    </div>
   
    
    </div>

 


</div>

);
}
export default MapApp;